body{
    padding: 5%;
    text-align: center;
}

.recipe-item{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 10px;
    padding: 5px;
}

.recipe-text{
    display: inline-block;
    text-align: left;
    padding-right: 10px;
    width: 400px;
}

.recipe-img{
    display: inline-block;
    right: 0;
    width:100px;
    height:100px;
    -o-object-fit: cover;
       object-fit: cover;
    border:1px solid #000;
    border-radius: 2px;
}

.star{
    cursor: pointer;
    font-size: 20px;
    margin-right: 15px;
}

.link{
    position: absolute;
    left:30px;
    top:30px;
}


